/* Google Fonts */
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700&display=swap');

/* CSS Reset */
@import url('./assets/reset/reset.css');

/* Global Styles */
body {
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: #363636;
  letter-spacing: 0.1px;
  position: relative;
  min-height: 100vh;
}

input {
  font-family: 'Montserrat', sans-serif;
}

.compony-title {
  font-weight: 700;
  font-size: 32px;
  line-height: 40px;
  color: #fff;
  margin-bottom: 60px;
}
